.kui-searchbar{position:relative;width:100%;max-width:320px}.kui-searchbar .kui-input{padding-left:44px}.kui-icon{-webkit-transition:opacity .3s;transition:opacity .3s}.kui-searchbar .kui-icon{position:absolute;top:50%;z-index:1;opacity:.3;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.kui-theme--light .kui-searchbar .kui-icon svg{fill:rgba(0,0,0,.55)}.kui-theme--dark .kui-searchbar .kui-icon svg{fill:hsla(0,0%,100%,.55)}.kui-searchbar--focused .kui-icon{opacity:1}.kui-searchbar__iconwrapper{-webkit-transition:opacity .3s;transition:opacity .3s}.kui-searchbar__iconwrapper .kui-icon{top:20px;left:12px;width:24px;height:24px}.kui-searchbar .kui-input__field{max-width:calc(100% - 24px)}.kui-searchbar .kui-input__line:before{max-width:calc(100% - 24px)}.kui-searchbar__dynamicicon{visibility:hidden;opacity:0;-webkit-transition:opacity .1s,visibility .1s;transition:opacity .1s,visibility .1s}.kui-searchbar__dynamicicon--visible{visibility:visible;opacity:1;-webkit-transition:opacity .1s;transition:opacity .1s}.kui-searchbar .kui-icon--close{right:10px;width:24px;height:24px}.kui-searchbar__label{position:absolute;width:1px;height:1px;margin:-1px;padding:0;border:0;clip:rect(0 0 0 0);overflow:hidden}